.container {
  margin: 0 auto;
  padding: 1.25rem 2.5rem;
  height: 100%;
  color: #135709;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.header {
  font-size: 1.5rem;
  margin: 0 auto;
  margin-bottom: 1rem;
}

.header__title {
  margin: 0;
}

.keyboard {
  display: block;
}

.keyboard__text {
  width: 100%;
  border-radius: 1rem;
  min-height: 15rem;
  margin-bottom: 1.25rem;
  resize: none;
  border: 2px solid #135709;
  padding: 1rem;
  font-size: 1.25rem;
  box-sizing: border-box;
  background-color: #A4DFCB;
  color: #135709;
}
.keyboard__text:hover, .keyboard__text:focus {
  outline: 2px solid #135709;
}

.keyboard__groupe {
  border: 2px solid #135709;
  border-radius: 1rem;
  padding: 1rem;
}

.key {
  min-width: 1rem;
  height: 2rem;
  border: 1px solid #135709;
  margin: 5px;
  padding: 1.5rem;
  background-color: #A4DFCB;
  color: #135709;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease-in, color 0.3s, box-shadow 0.5s;
}
.key[data-key-code=Space] {
  min-width: 20rem;
}
.key.active {
  background-color: #135709;
  color: #E7E6AB;
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
}
.key:active {
  background-color: #135709;
  color: #E7E6AB;
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
}
.key:hover {
  background-color: #135709;
  color: #E7E6AB;
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
}

.key__label {
  font-size: 1rem;
}

.keys__row {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer {
  margin: 0 auto;
}